.block--o .icon-row {
  margin-bottom: -1.3rem;
}
@media screen and (min-width: 768px) {
  .block--o .icon-row .media-right {
    flex-direction: row-reverse;
  }
}
.block--o .img-icon {
  width: 100%;
  margin-bottom: 0;
  margin-top: 0;
}
.block--o .img-icon img {
  width: 100%;
  height: 100px;
  object-fit: contain;
  object-position: left;
}